.messageInput {
    margin: 0 10px;
    background: #ffffff;
    border-radius: 4px;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.08);
    padding: 30px;
    margin-bottom: 30px;
    position: relative;

    textarea {
        width: 100%;
        height: 100px;
        background: #FDFDFD;
        border: 1px solid #eee !important;
        border-radius: 2px !important;
        resize: none;
        padding: 20px;
        outline: none !important;
        text-align: left;
    }

    .footer {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 10px;

        .warning {
            flex: 1;
            color: #F44336;
            font-weight: 500;
            height: 40px;
            line-height: 40px;
            font-size: 16px;
            padding-right: 20px;
            text-align: right;
        }

        .sendButton {
            background: #007bff;
            padding: 0 12px;
            height: 40px;
            line-height: 40px;
            color: #FFFFFF;
            font-weight: 500;
            border-radius: 3px;
            display: inline-block;
            transition: all 200ms ease;
            opacity: 0.4;
        }

        .sendButton:not(.disabled) {
            cursor: pointer;
            opacity: 1;
        }

        .sendButton:not(.disabled):hover {
            background: #0062cc;
            transform: translateY(-2px);
            box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
        }

        .sendButton:not(.disabled):focus {
            background: #0973e5;
        }
    }
    
    &::after {
        content: 'Posting Message';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 4px;
        background: rgba(255, 255, 255, 0.8);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        font-weight: 500;
        visibility: hidden;
        opacity: 0;
        transition: all 200ms ease;
    }

    &.loading::after {
        opacity: 1;
        visibility: visible;
    }
}
